import "./Timeline.css";
import { useState } from "react";
import classnames from "classnames";
import Collapse from "react-bootstrap/Collapse";
import Button from "react-bootstrap/Button";
import ProjectsCount from "./ProjectsCount";

const Timeline = ({ timeLineList }) => {
  const initialStateCollapse = timeLineList
    .filter((item) => item.projectsNames !== undefined)
    .reduce((a, v) => ({ ...a, [v.projectsNames.id]: false }), {});

  const [navState, setNavState] = useState(initialStateCollapse);
  const handleClick = (navId) =>
    setNavState((prev) => {
      return { ...prev, [navId]: !prev[navId] };
    });
  return (
    <div className="container">
      <div className={classnames("timeline")}>
        {timeLineList.map((item, index) => {
          return (
            <div className="event" data-date={item.year} key={index}>
              <h3>
                {item.company}
                {item.duration && (
                  <span className="ms-2 badge bg-primary">{item.duration}</span>
                )}
              </h3>
              <p>{item.role}</p>

              <div className="d-md-flex align-items-start flex-md-row">
                {item.projects &&
                  item.projects.map((pro, index) => {
                    return (
                      <ProjectsCount
                        count={pro.count}
                        text={pro.message}
                        key={index}
                      />
                    );
                  })}
              </div>
              {item.projectsNames && (
                <>
                  <Button
                    onClick={() => handleClick(item.projectsNames.id)}
                    aria-controls="example-collapse-text"
                    className="btn btn-secondary btn-sm"
                    id={`${index}_collapse`}
                  >
                    Projects ▼
                  </Button>
                  <Collapse in={navState[item.projectsNames.id]}>
                    <div
                      id={"timeline-collapse"}
                      className="p-2 text-monospace"
                    >
                      <ol className="list-group list-group-numbered">
                        {item.projectsNames.name.map((li, index) => {
                          return (
                            <li className="list-group-item" key={index}>
                              {li}
                            </li>
                          );
                        })}
                      </ol>
                    </div>
                  </Collapse>
                </>
              )}
            </div>
          );
        })}
      </div>
    </div>
  );
};
export default Timeline;
