import { useState } from "react";
import { connect } from "react-redux";
import store from "../Redux/store";
import classnames from "classnames";
import Header from "./Header/Header";
import Skill from "./Skills/Skill";
import Experience from "./Experience/Experience";

export const Portfolio = ({ Skills }) => {
  const [theme, setTheme] = useState(false);
  const themeChange = () => {
    theme ? setTheme(false) : setTheme(true);
    const body = document.body;
    let color = theme ? "dark" : "light";
    body.setAttribute("data-bs-theme", color);
    store.dispatch({ type: "THEME", payload: color });
  };
  return (
    <>
      <div className={classnames({ "bg-light": theme }, { "bg-dark": !theme })}>
        <nav className="navbar navbar-expand-sm  ">
          <div className={"container-fluid justify-content-end"}>
            <input
              type="checkbox"
              className="btn-check"
              id="btn-check-theme"
              onClick={themeChange}
            />
            <label
              className="btn btn-outline-body btn-sm align-items-center"
              htmlFor="btn-check-theme"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                className="bi bi-highlights"
                viewBox="0 0 16 16"
              >
                <path d="M16 8A8 8 0 1 0 0 8a8 8 0 0 0 16 0m-8 5v1H4.5a.5.5 0 0 0-.093.009A7 7 0 0 1 3.1 13zm0-1H2.255a7 7 0 0 1-.581-1H8zm-6.71-2a7 7 0 0 1-.22-1H8v1zM1 8q0-.51.07-1H8v1zm.29-2q.155-.519.384-1H8v1zm.965-2q.377-.54.846-1H8v1zm2.137-2A6.97 6.97 0 0 1 8 1v1z" />
              </svg>
            </label>
          </div>
        </nav>
        <Header />
        <div className="container-fluid mt-3">
          <div className="container py-3">
            <div className="row">
              <div className="col-lg-4 col-md-12 col-sm-12">
                <h1
                  className={classnames(
                    { "text-light": !theme },
                    { "text-dark": theme },
                    "display-6 text-capitalize fw-semibold py-1"
                  )}
                >
                  Expertises
                </h1>
                {Skills.map((object, index) => {
                  const { name, percentage, Data } = object;
                  return (
                    <Skill
                      key={index}
                      theme={theme}
                      title={name}
                      percentage={percentage}
                      Data={Data}
                    />
                  );
                })}
              </div>
              <div className="col-lg-8 col-md-12 col-sm-12 mt-2">
                <Experience />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container">
        <p className="p-2">Designed by Manikandan</p>
      </div>
    </>
  );
};

function mapStateToProps(state) {
  return {
    Skills: state.SkillsContainer.Container,
  };
}
export default connect(mapStateToProps)(Portfolio);
