import initialState from "./initialState";

const rootReducer = (state = initialState, action) => {
  switch (action.type) {
    case "THEME":
      return { ...state, Theme: action.payload };
    case "SET_MODAL_DATA":
      return {
        ...state,
        ModalContainer: action.payload,
      };
    default:
      return state;
  }
};
export default rootReducer;
