import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import { connect, useDispatch } from "react-redux";
import { setModalContent } from "../../Redux/actions";

const Model = ({ modal }) => {
  const dispatch = useDispatch();
  const handleOnclick = (item) => {
    const { name, image, link } = item;
    dispatch(
      setModalContent({ name: name, image: image, link: link, visible: false })
    );
  };
  return (
    <>
      <Modal
        show={modal.visible}
        onHide={() => handleOnclick(modal)}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header>
          <Modal.Title id="contained-modal-title-vcenter">
            {modal.name}
          </Modal.Title>
          <Button
            variant="outline-danger"
            size="sm"
            onClick={() => handleOnclick(modal)}
          >
            <strong>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                className="bi bi-x-lg"
                viewBox="0 0 16 16"
              >
                <path d="M2.146 2.854a.5.5 0 1 1 .708-.708L8 7.293l5.146-5.147a.5.5 0 0 1 .708.708L8.707 8l5.147 5.146a.5.5 0 0 1-.708.708L8 8.707l-5.146 5.147a.5.5 0 0 1-.708-.708L7.293 8z" />
              </svg>
            </strong>
          </Button>
        </Modal.Header>
        <Modal.Body>
          <img className="img-fluid" alt={modal.name} src={modal.image} />
          <hr />
          <a href={modal.link} target="_blank" className="btn  btn-secondary">
            View Online
          </a>
        </Modal.Body>
      </Modal>
    </>
  );
};

function mapStateToProps(state) {
  return {
    modal: state.ModalContainer,
  };
}
export default connect(mapStateToProps)(Model);
