import classnames from "classnames";
import { useDispatch } from "react-redux";
import { setModalContent } from "../../Redux/actions";
const Skill = ({ title, percentage, Data }) => {
  const dispatch = useDispatch();

  const handleOnclick = (item) => {
    const { Name, Image, Link, Icon } = item;
    dispatch(
      setModalContent({
        name: Name,
        image: Image,
        link: Link,
        visible: true,
        icon: Icon,
      })
    );
  };

  const badgeDataList = Data.map((item, index) => {
    const { Name, isCertificate, Icon } = item;
    return (
      <span key={index}>
        {isCertificate && (
          <>
            <button
              className="btn btn-sm text-bg-primary ms-1 p-1"
              onClick={() => handleOnclick(item)}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                viewBox="0 0 16 16"
              >
                <path d={Icon} />
              </svg>
              &nbsp;
              {Name}
            </button>
          </>
        )}
        {!isCertificate && (
          <span className="badge rounded-pill text-bg-primary ms-1 pl">
            {Name}
          </span>
        )}
      </span>
    );
  });
  return (
    <>
      <div className={classnames("mt-2")}>
        <h6 className="p-2">{title}</h6>
        {badgeDataList}
        <div className="pt-2">
          <div
            className="progress"
            role="progressbar"
            aria-label="Info example"
            aria-valuenow="80"
            aria-valuemin="0"
            aria-valuemax="100"
            style={{ height: "5px" }}
          >
            <div
              className="progress-bar bg-warning"
              style={{ width: percentage + "%" }}
            ></div>
          </div>
        </div>
      </div>
    </>
  );
};
export default Skill;
