import classnames from "classnames";
import { connect } from "react-redux";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import Timeline from "./Timeline";

export const Experience = ({ content }) => {
  return (
    <Tabs
      defaultActiveKey={content[0].tabName}
      className={classnames("mb-3 display-6 text-capitalize fw-semibold")}
      fill
      variant="pills"
      justify
    >
      {content.map((item, index) => {
        return (
          <Tab eventKey={item.tabName} title={item.name} key={index}>
            <Timeline timeLineList={item.content} />
          </Tab>
        );
      })}
    </Tabs>
  );
};

function mapStateToProps(state) {
  return {
    content: state.ExperienceContainer.Container,
  };
}
export default connect(mapStateToProps)(Experience);
